body {
  font-family: 'Lato', sans-serif;
}

.popover,
.bootstrap-datetimepicker-widget {
  z-index: 980;
}

.modal {
  z-index: 1100;
}

.btn-primary,
.note-actions .addnote {
  border-radius: 4px;
  background-color: #232d7d;
  border: 0;
  color: white;
}

.modal-footer .btn-default,
.form-custom-actions .btn-default {
  border-radius: 4px;
  background-color: white;
  color: #232d7d;
  border: 0;
  box-shadow: inset 0px 0px 0px 1px #232d7d;
}

.btn-primary:hover,
.note-actions .addnote:hover {
  background-color: #4651a7;
}

.tooltip {
  position: fixed;
}

.tooltip-arrow {
  display: none;
}

.tooltip-inner {
  color: black;
  background: white;
  border: 0;
  border-radius: 4px;
  filter: drop-shadow(3px 3px 3px black);
}
